body {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    color: white;
  }
  
  .app {
    width:500px;
    height:220px;
    background-color: #5e345f;
    border-radius: 15px;
    padding: 20px;
    box-shadow: 10px 10px 42px 0px rgba(0, 0, 0, 0.75);
    display: flex;
    justify-content: space-evenly;
  }
  
  .score-section {
    text-align: center;
  }
  
  .question-section {
    width: 100%;
  }

  .question-section>h5{
    display: inline;
    margin-bottom:10px;
  }
  
  .question-count {
    margin-top: 12px;
    margin-bottom: 20px;
  }
  
  .question-count span {
    font-size: 28px;
  }
  
  .question-text {
    margin-bottom: 12px;
  }

  .custom-back{
    margin-top:50px;
  }


  .custom-back>a{
    text-decoration: none;
    font-size:18px;
    color:white;
  }
  
  /* ANSWERS/RIGHT SECTION */
  .answer-section {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  
  button {
    width: 100%;
    font-size: 16px;
    color: #ffffff;
    background-color: #4a2543;
    border-radius: 15px;
    display: flex;
    flex-direction: column;
    padding: 5px;
    justify-content: flex-start;
    align-items: center;
    border: 5px solid #234668;
    cursor: pointer;
  }
  
  .correct {
    background-color: #2f922f;
  }

  .red{
    background-color: red;
  }
  
  button:hover {
    background-color: #555e7d;
  }
  
  button:focus {
    outline: none;
  }
  
  button svg {
    margin-right: 5px;
  }
  .actions {
    display: flex;
    margin-top: 15px;
  }
  .actions button {
    background-color: #7cc6fe;
    justify-content: center;
  }
  .actions button:hover {
    background-color: #234668;
    color: white;
  }


@media screen and (max-width:540px){
  body{
    overflow: auto;
  }
  .app{
    width: 90%;
    height:100%;
    display: grid;
    grid-template-rows: 32% 68%;
  }

  .question-section{
    font-size: 21px;
  }

  .answer-section{
    font-size: 21px;
    margin-bottom: 20px;
  }

  .button{
    width:100%;
    font-size: 25px;
    margin:auto;
  }

  .actions{
    display: grid;
    grid-template-rows: 50% 50%;
    font-size:25px;
    margin-bottom:10px; 
  }

  .actions>button{
    font-size: 25px;
    margin-bottom: 5px;
  }


  .custom-back{
    display: none;
  }
}